html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

root {
  --pale-blue: #597CA6;
  --brown: #A68359;
}

@font-face {
  font-family: "Timeburner";
  src: url("../fonts/Timeburner-xJB8.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.container-menu {
  max-width: 41rem;
  max-height: 4.375rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container-menu {
    padding: 0;
  }
}

.nav-wrap {
  overflow: hidden;
  cursor: pointer;
  height: 100%;
  z-index: 30;
}

.nav-left {
  width: 2.1875rem;
  height: 2.1875rem;
  background-color: #A68359;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-13.0625rem);
  z-index: 30;
}

.nav-left:hover {
  background-color: #b59875;
}

.nav-left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.nav--label--table {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-right {
  width: 21.875rem;
  height: 2.1875rem;
  background-color: #7491b4;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 30;
}

.nav--label {
  padding: 0.46875rem 1.28125rem;
  text-align: center;
  color: #000;
  font-family: "Timeburner", sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  text-decoration: none;
}

.nav--label:hover {
  text-decoration: underline;
  line-height: 138%;
  background-color: #89a2c0;
  height: 100%;
}

.card {
  padding: 1.875rem;
}

html, body {
  margin: 0;
  padding: 0;
  overflow-x: auto;
  transform-origin: top left;
  max-width: 100vw;
  max-height: 100vh;
}

body::-webkit-scrollbar {
  display: none;
}

.studio-logo img {
  align-items: center;
  justify-content: center;
  object-fit: fill;
  top: 0;
  left: 0;
  overflow: hidden;
}

.studio-logo {
  flex-direction: column;
  display: flex;
  width: 100vw;
  height: 100vh;
  object-fit: fill;
  position: relative;
  z-index: 20;
  margin-bottom: 0;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.studio-logo-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; /* Make sure it fills `.studio-logo` */
  object-fit: fill;
  z-index: 10;
}

body, html {
  margin: 0;
  padding: 0;
  overflow-x: auto;
  max-width: 100vw;
  max-height: 100vh;
}

.fullscreen-gradient {
  position: relative;
}

.scrolling-words-container {
  display: flex;
  height: 8vh;
  align-items: center;
  justify-content: space-between;
  padding: 0.625rem 1.875rem;
  font-size: 6vw;
  font-weight: 600;
  overflow: hidden;
  gap: 1vw;
}

.non-scrolling-words-box {
  text-align: center;
  font-weight: 600;
  white-space: nowrap;
  position: absolute;
  top: 40%;
  left: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.scrolling-words-box {
  height: 14vh;
  position: absolute;
  margin: auto;
  overflow: hidden;
  text-align: center;
  top: 40%;
  right: 5%;
  transform: translateX(-20rem);
}
.scrolling-words-box ul {
  margin: 0 1rem;
  padding: 0;
  animation: scrollUp 40s;
}
.scrolling-words-box ul li {
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  list-style: none;
}

@keyframes scrollUp {
  2.5%, 12.5% {
    transform: translateY(-12.5%);
  }
  15%, 25% {
    transform: translateY(-25%);
  }
  27.5%, 37.5% {
    transform: translateY(-37.5%);
  }
  40%, 50% {
    transform: translateY(-50%);
  }
  52.5%, 62.5% {
    transform: translateY(-62.5%);
  }
  65%, 75% {
    transform: translateY(-75%);
  }
  77.5%, 87.5% {
    transform: translateY(-87.5%);
  }
}
.latest-project-container {
  background-image: url("/design/Library_hero.png");
  aspect-ratio: 96/31;
  background-size: cover;
  position: relative;
}

.meowfficer-logo {
  height: 50%;
  flex-shrink: 0;
  aspect-ratio: 677/275;
  position: absolute;
  left: 5%;
  top: 1%;
}

.steam-logo {
  height: 10%;
  flex-shrink: 0;
  aspect-ratio: 131/67;
  border-radius: 10px;
  background: #FFF;
  position: absolute;
  bottom: 35%;
  left: 5%;
}

.bottom-nav-container {
  background-color: #597CA6;
  background-size: cover;
  display: flex;
  justify-content: space-between;
  padding: 0.625rem 1.875rem;
  height: 3.75rem;
}

.studio-font {
  transform: translateX(-2rem);
  height: 3.125rem;
  aspect-ratio: 74/25;
  align-self: center;
}

.bottom-links {
  align-items: center;
  display: flex;
  gap: 2.1875rem;
}

.nav-bottom--label img[alt=side_table_png] {
  height: 1.25rem;
  transform: translateX(0.1875rem);
}

.bottom-links--socials {
  height: 1.1875rem;
  text-decoration: none;
}

.insta-bottom {
  display: inline-block;
}

.insta-bottom img {
  aspect-ratio: 640/427;
  height: 1.1875rem;
}

.etsy-bottom img {
  height: 1.03125rem;
  width: 1.03125rem;
  aspect-ratio: 1/1;
  transform: translateY(-0.0625rem);
}

.nav-bottom--label img {
  vertical-align: middle;
}

.nav-bottom--label {
  color: #000;
  font-family: "Timeburner", sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  text-decoration: none;
  display: inline-flex;
  margin: 0;
  align-items: center;
}